@import "reset";
@import "fonts";

@keyframes square-hide {
  0% {
    transform: scale(1);
  }
  25% {
    transform: scale(0.95);
  }
  50% {
    transform: scale(1);
  }
  100% {
    transform: scale(0);
  }
}
@keyframes square-show {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}

input {
  height: 40px;
  &:-webkit-autofill {
    -webkit-box-shadow: inset 0 0 0 50px #fff !important;
  }
}
input,
button {
  outline: none;
  box-shadow: none;
  font: 14px/1em "Source Sans Pro Light", sans-serif;
}
button {
  cursor: pointer;
}
.link {
  text-decoration: none;
  font-style: italic;
  &:hover {
    text-decoration: underline;
  }
  &:active {
    text-decoration: underline;
  }
  &:focus {
    text-decoration: underline;
  }
}
.white-link {
  color: #fff;
  &:hover {
    color: #fff;
  }
  &:active {
    color: #fff;
  }
  &:focus {
    color: #fff;
  }
  &:visited {
    color: #fff;
  }
}
.black-link {
  color: #191919;
  &:hover {
    color: #191919;
  }
  &:active {
    color: #191919;
  }
  &:focus {
    color: #191919;
  }
  &:visited {
    color: #191919;
  }
}
.btn-link {
  border: none;
  background: none;
  width: auto;
  margin: 0 auto;
}
body {
  font:  14px/1.6em 'Source Sans Pro light', sans-serif;
  letter-spacing: .6px;
  color: #191919;
  height: 100%;
  width: 100%;
}
.grid-block {
  display: grid;
  display: -ms-grid;
}
.grid-2-columns {
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;
  -ms-grid-columns: 1fr 1fr;
}
.flex-center {
  display: flex;
  display: -ms-flex;
  justify-content: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}
#no-support-msg {
  display: none;
  text-transform: uppercase;
  line-height: 1.5em;
  margin: 20px;
  background-color: rgba(255,255,255,0);
}
html:root {
  #no-support-msg {
    display: block\0;
  }
  .login-page {
    display: none\0;
  }
}
.btn {
  background-color: #191919;
  color: #fff;
  width: 100%;
  padding: 10px;
  border: 1px solid #191919;
  height: 40px;
  transition: 0.5s ease-in-out;
  text-transform: uppercase;
  letter-spacing: 2px;
  cursor: pointer;
  &:hover {
    background-color: #fff;
    color: #191919;
  }
  &:focus {
    background-color: #fff;
    color: #191919;
  }
  &:active {
    background-color: #fff;
    color: #191919;
  }
}
.file {
  &::before {
    background-color: #fff;
    min-width: 100px;
    border: 1px solid #191919;
    height: 40px;
    transition: 0.5s ease-in-out;
    text-transform: uppercase;
    letter-spacing: 2px;
    cursor: pointer;
    content: 'Upload your photo';
    width: 100%;
    display: grid;
    display: -ms-grid;
    justify-content: center;
    align-items: center;
    position: absolute;
    z-index: 1;
    -webkit-user-select: none;
    box-sizing: border-box;
  }
  &:hover {
    &::before {
      background-color: #191919;
      color: #fff;
    }
  }
  z-index: 0;
  position: relative;
  &::-webkit-file-upload-button {
    visibility: hidden;
  }
  + {
    span {
      text-align: center;
    }
  }
}
.white-btn {
  &:hover {
    background-color: #191919;
    color: #fff;
  }
}
.wrapper {
  height: 100vh;
  width: 100vw;
  position: relative;
  background: linear-gradient(-45deg, rgba(247,247,247,1) 60%, rgba(255,255,255,1) 60%, rgba(255,255,255,0) 100%);
}
.preloader {
  height: 100%;
  width: 100%;
  background-color: #fff;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999;
  img {
    width: 80px;
  }
}
.login-page {
  width: 100%;
  height: 100%;
  overflow: hidden;
  box-sizing: border-box;
  scroll-behavior: smooth;
  background-color: #fff;
  border: 1px solid #191919;
}
.login {
  width: 300px;
}
.animation-hide {
  animation: square-hide 0.7s ease-in-out both;
}
.animation-show {
  animation: square-show 0.7s ease-in-out both;
}
.user-login {
  width: 600px;
  height: 100%;
  .col {
    padding: 20px;
    &:first-child {
      grid-row: 1;
      grid-column: 1;
    }
    &:last-child {
      grid-row: 1;
      grid-column: 2;
    }
  }
}
.form {
  width: 100%;
  text-align: center;
  .form__item {
    display: block;
    width: 100%;
  }
}
.form__item {
  border: none;
  letter-spacing: .6px;
  outline: none;
  box-sizing: border-box;
  &:not(.file) {
    border-bottom: 1px solid #191919;
  }
  &::-webkit-input-placeholder {
    color: #191919;
  }
  &::-moz-placeholder {
    color: #191919;
  }
  &:-ms-input-placeholder {
    color: #191919;
  }
  &:-moz-placeholder {
    color: #191919;
  }
  &:focus {
    &::-webkit-input-placeholder {
      opacity: 0;
    }
    &::-moz-placeholder {
      opacity: 0;
    }
    &:-ms-input-placeholder {
      opacity: 0;
    }
    &:-moz-placeholder {
      opacity: 0;
    }
  }
}
.task-form {
  grid-template-columns: 65% 35%;
  -ms-grid-columns: 65% 35%;
  grid-template-rows: 1fr;
  .form__item {
    font-size: 14px;
  }
}
.error {
  display: block;
  width: 100%;
  font-size: 11px;
  font-style: italic;
  height: 20px;
  line-height: 2em;
  text-align: left;
}
.psw-group {
  position: relative;
}
.eye {
  position: absolute;
  width: 18px;
  height: 18px;
  bottom: 28px;
  right: 0;
  visibility: hidden;
  &:before {
    content: "";
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    visibility: visible;
    background: url("../img/eye.svg") no-repeat;
  }
  &:after {
    content: "";
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    visibility: visible;
  }
  &:checked {
    &:after {
      background: url("../img/hide.svg") no-repeat;
    }
  }
}
.form__title {
  padding: 10px 0 20px;
  text-transform: uppercase;
}
.form__link {
  margin: 15px 0;
}
.user-account__header {
  background-color: #191919;
  color: #fff;
  height: 45px;
  z-index: 100;
  align-items: center;
  h1 {
    font-weight: 300;
    font-size: 22px;
    word-spacing: -3px;
    line-height: 1em;
    display: inline-block;
    vertical-align: middle;
  }
  .col {
    text-align: left;
    &:last-child {
      text-align: right;
      padding-right: 10px;
    }
  }
}
footer {
  background-color: #191919;
  color: #fff;
  height: 45px;
  z-index: 100;
  p {
    font-style: italic;
  }
}
.logo {
  display: inline-block;
  vertical-align: middle;
  width: 15px;
  margin-left: 10px;
  border: 1px solid #fff;
  padding: 5px;
  border-radius: 50%;
}
.user-account {
  display: none;
}
.user-info {
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
  -ms-grid-columns: 1fr;
  padding: 15px 0 0 45px;
}
.user-account__section {
  min-height: calc(100vh - 90px);
}
.user-info-container {
  position: relative;
  height: 100%;
  background-color: rgba(255,255,255,0);
  padding-left: 20px;
  z-index: 1;
  &:before {
    content: "";
    display: block;
    position: absolute;
    height: 100%;
    top: 0;
    z-index: -1;
    width: 45px;
    left: 0;
    background-color: #FFF0F5;
  }
}
.user-info__img {
  width: 120px;
  height: 120px;
  position: relative;
  z-index: 1;
  margin-bottom: 10px;
  &:after {
    position: absolute;
    content: "";
    display: block;
    width: 200px;
    height: 20px;
    top: 50%;
    margin-top: -10px;
    margin-left: -40px;
    border-top: 1px solid #191919;
    border-bottom: 1px solid #191919;
    transform: skewX(-50deg);
    z-index: -1;
    transition: 0.5s ease-in-out;
  }
  .avatar {
    position: absolute;
  }
  &:hover {
    &:after {
      transform: skewX(50deg);
    }
  }
}
.avatar {
  width: 120px;
  height: 120px;
  background-color: #fff;
  box-sizing: border-box;
  overflow: hidden;
  border: 1px solid #191919;
  border-radius: 50%;
  margin: 0 auto;
  img {
    max-height: 200px;
    margin-top: 50%;
    margin-left: 50%;
    transform: translate(-50%,-50%);
  }
}
.subtitle {
  font-size: 22px;
  display: inline-block;
  font-weight: 300;
  line-height: 1.2em;
}
.form__title.subtitle {
  &:after {
    content: "";
    display: block;
    background-color: #FFF0F5;
    width: 100%;
    height: 25px;
    margin: -12px -5px 0;
    padding: 0 5px;
  }
}
.title {
  font-weight: 300;
  display: inline-block;
  line-height: 1.2em;
  background-color: #FFF0F5;
  padding: 10px;
  margin-bottom: 30px;
  font-size: 20px;
  span {
    text-transform: lowercase;
  }
}
.sign-in-form {
  margin-bottom: 40px;
}
.user-info__title {
  padding-right: 20px;
  h1 {
    color: #f7f7f7;
    line-height: 1em;
    font-size: 42px;
    font-weight: 900;
    letter-spacing: 0;
    -webkit-text-fill-color: #f7f7f7;
    -webkit-text-stroke-color: #191919;
    -webkit-text-stroke-width: 1px;
  }
  h2 {
    font-size: 20px;
    padding-top: 10px;
  }
}
.quotes {
  display: none;
  min-height: 30px;
  padding: 20px 25px 15px;
  margin-top: 30px;
  position: relative;
  background-color: #FFF0F5;
  &:before {
    content: "";
    display: block;
    width: 30px;
    height: 30px;
    position: absolute;
    background: url(../img/left-quote.svg) no-repeat;
    top: -10px;
    left: -8px;
  }
  &:after {
    content: "";
    display: block;
    width: 30px;
    height: 30px;
    position: absolute;
    background: url(../img/left-quote.svg) no-repeat;
    transform: rotate(-180deg);
    bottom: -10px;
    right: -8px;
  }
}
.quote {
  display: none;
  font-size: 18px;
  line-height: 1.2em;
}
.task-container {
  position: relative;
  padding: 0 20px 20px 65px;
  height: 100%;
  box-sizing: border-box;
  &:before {
    content: "";
    display: block;
    position: absolute;
    height: 100%;
    top: 0;
    width: 45px;
    left: 0;
    background-color: #FFF0F5;
  }
}
.tasks {
  height: 100%;
  position: relative;
  max-height: calc(50vh - 70px);
  overflow-y: auto;
  border: 1px solid #191919;
  ul {
    margin-left: 0;
    padding-left: 0;
  }
  &::-webkit-scrollbar-button {
    width: 5px;
    height: 0;
  }
  &::-webkit-scrollbar-track {
    background-color: #fff;
    border-bottom: 1px solid #191919;
  }
  &::-webkit-scrollbar-thumb {
    border-radius: 0;
    background-color: #191919;
    &:hover {
      background-color: #191919;
    }
  }
  &::-webkit-resizer {
    background-image: none;
    background-repeat: no-repeat;
    width: 5px;
    height: 0;
  }
  &::-webkit-scrollbar {
    width: 5px;
  }
}
.tasks-filter {
  padding: 10px 0;
  button {
    display: inline-block;
  }
}
.close-tasks {
  position: relative;
  top: 2px;
  float: right;
}
.task {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-bottom: 10px;
  list-style-type: none;
  text-align: left;
  padding: 10px 20px;
}
.new {
  background-color: #f7f7f7;
  .checkmark {
    background-color: #fff;
  }
}
.done {
  background-color: #FFF0F5;
  input {
    &:checked {
      + {
        .checkmark {
          background-color: #191919;
        }
      }
    }
  }
  .checkmark {
    background-color: #191919;
  }
}
.hidden {
  display: none;
}
.visible {
  display: block;
}
.task__text {
  display: inline-block;
  padding: 0 20px;
  font-size: 18px;
  outline: none;
}
.task__mark {
  display: inline-block;
  cursor: pointer;
  user-select: none;
  padding: 5px;
  position: relative;
  width: 20px;
  height: 20px;
  input {
    position: relative;
    width: 20px;
    height: 20px;
    opacity: 0;
    cursor: pointer;
  }
}
.checkmark {
  display: block;
  position: absolute;
  border: 1px solid #191919;
  background: #fff url(../img/checked.svg) center center no-repeat;
  background-size: 20px;
  z-index: 0;
  border-radius: 50%;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.task-close {
  margin-left: auto;
  cursor: pointer;
}
@media screen and (-ms-high-contrast: active) {
  #no-support-msg {
    display: block;
  }
  .login-page {
    display: none;
  }
  .flex-center {
    display: block;
  }
}
@media screen and (-ms-high-contrast: none) {
  #no-support-msg {
    display: block;
  }
  .login-page {
    display: none;
  }
  .flex-center {
    display: block;
  }
}
@media screen and (max-width: 1023px) {
  .user-account__section {
    grid-template-columns: 1fr;
    -ms-grid-columns: 1fr;
    grid-template-rows: 1fr 1fr;
  }
}
@media screen and (max-width: 600px) {
  .user-account__section {
    grid-template-rows: 38% 62%;
  }
}
@media screen and (min-width: 600px) {
  .logo {
    width: 18px;
    margin-left: 20px;
  }
  .login {
    width: 500px;
  }
  .user-login {
    width: 1000px;
  }
  .form {
    width: 300px;
  }
  .user-info-container {
    &:before {
      width: 80px;
    }
  }
  .user-info__title {
    h1 {
      font-size: 52px;
    }
    h2 {
      font-size: 24px;
      padding-top: 15px;
    }
  }
  .form__title {
    letter-spacing: 1.2px;
  }
  .task-container {
    padding-left: 100px;
    &:before {
      width: 80px;
    }
  }
  .quotes {
    display: block;
  }
  .tasks {
    max-height: calc(50vh - 150px);
  }
  .task-form {
    .form__item {
      font-size: 18px;
      color: #191919;
    }
    grid-template-columns: 70% 30%;
    -ms-grid-columns: 70% 30%;
  }
  .avatar {
    width: 140px;
    height: 140px;
  }
  .user-info__img {
    width: 140px;
    height: 140px;
    margin-bottom: 20px;
    &:after {
      width: 260px;
      margin-left: -60px;
    }
  }
  .user-info {
    padding: 30px 0 20px 80px;
  }
  .btn {
    padding: 10px 20px;
  }
  .user-account__header {
    .col {
      &:last-child {
        padding-right: 15px;
      }
    }
    h1 {
      font-size: 26px;
    }
  }
  .subtitle {
    font-size: 24px;
  }
  .title {
    font-size: 22px;
  }
}
@media screen and (min-width: 375px) {
  .login {
    height: 600px;
    border: 1px solid #191919;
  }
}
@media screen and (min-width: 1024px) {
  .quotes {
    min-height: 50px;
  }
  .task-container {
    border: 20px solid #f7f7f7;
    padding: 20px;
    &:before {
      display: none;
    }
  }
  .tasks {
    min-height: calc(100vh - 255px);
  }
}
@media screen and (min-width: 1200px) {
  .user-info__img {
    left: -70px;
    margin-bottom: 0;
  }
  .user-info {
    grid-template-columns: 1fr 4fr;
    -ms-grid-columns: 1fr 4fr;
    padding: 50px 0 50px 150px;
  }
  .user-info-container {
    &:before {
      width: 170px;
    }
  }
  .user-info__title {
    h2 {
      font-size: 28px;
    }
    padding-left: 20px;
  }
  .task-form {
    grid-template-columns: 80% 20%;
    -ms-grid-columns: 80% 20%;
  }
}
